import EstruturaImpactos from "../EstruturaImpactos"
import './Impactos.css'
import '../../variaveis.css'

const SessaoImpactos = () => {
    return (
        <section className="containerImpactos">
            <h1>A Frmula E  o Futuro!</h1>
            <EstruturaImpactos
                imgSrc= {`${process.env.PUBLIC_URL}/imagens/img-sustentabilidade.svg`}
                imgAlt= "Foto de lmpadas">
                    <article>
                        <h2>Sustentabilidade</h2>
                        <p>A Frmula E est mudando o futuro dos carros de corrida, devido a sustentabilidade que oferece, usando motores eltricos.</p>
                    </article>
            </EstruturaImpactos>
            <EstruturaImpactos
                imgSrc= {`${process.env.PUBLIC_URL}/imagens/img-inovacao.svg`}
                imgAlt= "Foto de livros">
                    <article>
                        <h2>Inovao</h2>
                        <p> o que a Frmula E representa, sempre inovando com seus carros eltricos e baterias de alta performance.</p>
                    </article>
            </EstruturaImpactos>
            <EstruturaImpactos
                imgSrc= {`${process.env.PUBLIC_URL}/imagens/img-competitividade.svg`}
                imgAlt= "Foto ">
                    <article>
                        <h2>Competitividade</h2>
                        <p>Um dos fatores que torna a categoria mais emocionante e divertida. Usando carros de alto desempenho, focando na eficincia.</p>
                    </article>
            </EstruturaImpactos>
        </section>
    )
}

export default SessaoImpactos