import './SessaoFormulaE.css'
import '../../variaveis.css'

const SessaoFormulaE = () => {
    return (
        <section className='containerFormula'>
            <article className='textoFormula'>
                <h1>O que  a Frmula E?</h1>
                <p>A Frmula E  um campeonato mundial de automobilismo, organizado pela FIA, em que os carros so eltricos e as corridas acontecem em circuitos urbanos. O ponto de destaque  que os carros eltricos utilizados na competio no emitem poluentes atmosfricos durante o seu funcionamento, o que desempenha um papel crucial na reduo da poluio do ar e na luta contra as mudanas climticas.</p>
            </article>
            <div className='containerImg'>
                <img 
                    src={`${process.env.PUBLIC_URL}/imagens/img-formulaE.svg`}
                    alt='Foto de um carro da frmula E vermelho'
                    className='imgFormula'/>
            </div>
            
        </section>
    )
}

export default SessaoFormulaE