import './Forum.css'
import '../../variaveis.css'
import Card from '../elementos/Card'
import Botao from '../elementos/Botao'

const AbaForum = () => {
    return(
        <section className='containerForum'>
            <h1>Junte-se  comunidade de apaixonados por velocidade!</h1>
            <p className='subtitulo'>Faa parte da nossa comunidade e compartilhe sua paixo pelo automobilismo.</p>
            <article className='cards'>
                <Card
                    imgSrc= {`${process.env.PUBLIC_URL}/imagens/foto-forum-1.svg`}
                    imgAlt="Foto do piloto Jordan King"
                    nome="Jordan King">
                        <p>
                            Os carros da Frmula E esto evoluindo rapidamente! A nova gerao tem uma eficincia energtica incrvel.
                        </p>
                </Card>
                <Card
                    imgSrc= {`${process.env.PUBLIC_URL}/imagens/foto-forum-2.svg`}
                    imgAlt="Foto do piloto Edoardo Mortara"
                    nome="Edoardo Mortara">
                        <p>
                            Eu adoro as pistas urbanas da Frmula E! Elas do um toque nico e aproximam a corrida do pblico.
                        </p>
                </Card>
                <Card
                    imgSrc={`${process.env.PUBLIC_URL}/imagens/foto-forum-3.svg`}
                    imgAlt="Foto do piloto Nyck de Vries"
                    nome="Nyck de Vries">
                        <p>
                             impressionante como a Frmula E est influenciando o desenvolvimento de carros eltricos comerciais.
                        </p>
                </Card>
            </article>
            <Botao span="Fazer parte da comunidade"/>
        </section>
    )
}

export default AbaForum