import './Bpm.css'
import '../../variaveis.css'
import Barras from '../graficos/Barras'
import Botao from '../elementos/Botao'

const SessaoBPM = () => {
    
    return (
        <section className='containerBPM'>
            <Barras />
            <article className='textoBPM'>
                <h1>Acompanhe a emoo!</h1>
                <p>
                    Veja em tempo real como os batimentos cardacos dos espectadores reagem s emoes intensas da corrida! O grfico mostra os picos de adrenalina durante os momentos mais emocionantes da Frmula E.
                </p>
            </article>
            <div className='botaoBPM'>
                <Botao 
                    span="Ver mais detalhes"
                />
            </div>
        </section>
    )
}

export default SessaoBPM