import './Recompensas.css'
import '../../variaveis.css'

const SessaoRecompensas = () => {
    return (
        <section className='containerReco'>
            <h2>E o melhor de tudo:</h2>
            <h1>Voc pode ganhar premiaes por suas ideias!!!</h1>
            <article className='textosPremios'>
                <p className='p1'>
                    Caso voc tenha alguma ideia de inovao e melhoria para a Frmula E, voc tem a oportunidade de envi-las s escuderias da competio e concorrer a prmios.
                </p>
                <p className='p2'>
                    Se a sua ideia for escolhida em algum momento, alm da premiao em dinheiro, voc ainda pode receber o convite para participar da implementao dessa melhoria.
                </p>
                <p className='pBorda'>
                    J imaginou estar ao lado dos engenheiros e mecnicos da Frmula E e, depois, assistir os carros correndo com uma ideia sua? Um sonho, no  mesmo?
                </p>
                <img src= {`${process.env.PUBLIC_URL}/imagens/ilustracao-tech.svg`}/>
            </article>
        </section>
    )
}

export default SessaoRecompensas