import CaixaImgText from '../elementos/CaixaImgText'
import './Solucao.css'
import '../../variaveis.css'

const SessaoSolucao = () => {
        return (
            <section className='containerSolucao'>
                <h2>Aqui voc pode:</h2>
                <article className='caixas'>
                    <CaixaImgText
                            imgSrc= {`${process.env.PUBLIC_URL}/imagens/icon-search.svg`}
                            imgAlt='icone de uma lupa de pesquisar'>
                        <p>
                            Explorar o carro da Frmula E e entender seu funcionamento, as especificaes de suas peas, as inovaes por trs do automvel e as novas tecnologias criadas.
                        </p>
                    </CaixaImgText>
                    <CaixaImgText
                        imgSrc= {`${process.env.PUBLIC_URL}/imagens/icon-compare.svg`}
                        imgAlt='icone que representa a comparao entre dois elementos'>
                        <p>
                            Comparar os carros da Frmula E e Frmula 1, entender suas diferenas e semelhanas. 
                        </p>
                    </CaixaImgText>
                </article>
            </section>
        )
}

export default SessaoSolucao